.container {
    height:100%;
    @include animate;
    
    &.parallax {
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-position: center center;
        -webkit-background-size:cover;
        -moz-background-size:cover;
        -ms-background-size:cover;
        -o-background-size:cover;
        background-size:cover;
        height:100%;
        justify-content: center;
        position:relative;
        @include animate;
        &:before {
            content:" ";
            position:absolute;
            top:0;
            right:0;
            bottom:0;
            left:0;
            opacity:.8;
        }
    }

    &.flex {
        display:flex;
        align-items:center;

        @include tablet {
            display:block;
        }

        .fullWidth {
            flex:1;
            justify-content: center;
            flex-direction: column;
        }

        .quarter {
            display: flex;
            flex-direction: column;
            &.text {
                width:calc(40% - 100px);

                @include tablet {
                    width:calc(100% - 100px);
                }
            
            }
            &.image {
                width:40%;

                @include tablet {
                    width:50%;
                }
            
            }
            
        }

        .half {
            display: flex;
            flex-direction: column;
            width:50%;
            &.text {
                width:calc(50% - 100px);

                div {
                    float:left;
                }
            }
        }

        .three-quarter {
            display: flex;
            flex-direction: column;
            &.text {
                width:calc(60% - 100px);
            }
            &.image {
                width:60%
            }
        }

    }

    .image {
        height:100%;
        justify-content:center;
        @include animate;
        /* @include tablet {
            h2, h3 {
                display:none;
            }
        }
        @include tablet {
            display:none !important;
        } */
    }

    .text {
        padding:0 50px;
        background:$white;
        height:100%;
        justify-content:center;
        box-shadow: 0 0 20px $black;
        position:relative;
        z-index:1;

        @include tablet {
            background:transparent;
            box-shadow:none;

            &.right {
                text-align:right;
            }

            &.left {
                text-align:left;
            }
        }

        @include mobile-portrait {
            background:transparent;
            box-shadow:none;

            &.right {
                text-align:center;
            }

            &.left {
                text-align:center;
            }
        }
    }

    .arrow {
        width:100%;
        position:absolute;
        bottom:0;
        text-align:center;
    }

    div.red {
        background-color:$red;
    }
    div.orange {
        background-color:$orange;
    }
    div.yellow {
        background-color:$yellow;
    }
    div.green {
        background-color:$green;
    }
    div.blue {
        background-color:$blue;
    }
    div.purple {
        background-color:$purple;
    }

    div.red-fade {
        background: linear-gradient($red, $orange);
    }
    div.orange-fade {
        background: linear-gradient($orange, $yellow);
    }
    div.yellow-fade {
        background: linear-gradient($yellow, $green);
    }
    div.green-fade {
        background: linear-gradient($green, $blue);
    }
    div.blue-fade {
        background: linear-gradient($blue, $purple);
    }
    div.purple-fade {
        background: linear-gradient($purple, $black);
    }
}